import axios from "axios";

// Cria√ß√£o da inst√¢ncia do Axios
const api = axios.create({
  baseURL: "https://pfc-backend-production.up.railway.app/",
  headers: {
    "Content-Type": "application/json",
  },
});

// Adicionando um interceptor para incluir o token de autentica√ß√£o nas requisi√ß√µes
api.interceptors.request.use(
  (config) => {
    // Obt√©m o token do localStorage
    const token = localStorage.getItem("token");

    // Se o token estiver presente, adiciona ao cabe√ßalho Authorization
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default api;
